.card {
  width: 1240px;

  .card-content-wrapper {
    display: flex;
  }
}

.card :global(.ant-card-body) {
  // reset inner padding
  padding: 0;
  padding-top: 1px;
}

.mono {
  font-family: monospace;
}

.right {
  text-align: right;
}

.null-name {
  color: #aaa;
}

.card :global(td.ant-table-cell) {
  padding-top: 4px !important;
  padding-bottom: 4px !important;
}

.ignore-pointer-events {
  pointer-events: none;
}

// override for selected rows
tr.focused-row td {
  background-color: yellow !important;
}

.table {
  border-right: 1px solid #f0f0f0;
}
